<template>
    <div class="introPage">
        <div class="viewC" :class="{'act':viewCFlag}" :style="{backgroundImage:'url('+firstPart.bannerImg+')'}">
            <div class="introLogo" :style="{backgroundImage:'url('+firstPart.content.logo+')'}"></div>
            <div class="introContent">
                <p v-for="(itemText,index) in firstPart.content.content" :key="index" v-html="itemText"></p>
            </div>
        </div>
        <div class="introMain">
            <div class="introLogo" :style="{backgroundImage:'url('+secondPart.logo+')'}"></div>
            <div class="introContent">
                <p v-for="(item,index) in secondPart.content" :key="index" :class="{'cn':item.type === 'chinese'}" v-html="item.content"></p>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'introPage',
    data() {
      return {
        viewCFlag: false,
        firstPart: {
          bannerImg: require('../assets/img/introMainView.png'),  // require('../assets/img/introMainView.png')
          content: {
            logo: require('../assets/img/introLogo_01.png'), // require('../assets/img/introLogo_01.png')
            content: [
              'Tibetan production and transportation to the mainland, the whole production, manufacturing and assembly process for Tibetan alpine snow original products <br> The fresh air from Himalaya can still be touched for the first time after opening the product gently.',
              'Tibetan production and transportation to the mainland, the whole production, manufacturing and assembly process for Tibetan alpine sn original products <br> The fresh air from Himalaya can still be touched for the first time after opening the product gently.'
            ]
          }
        },
        secondPart: {
          logo: require('../assets/img/introLogo_01.png'), // require('../assets/img/introLogo_01.png')
          content: [
            {
              type: 'english',
              content: 'Tibetan production and transportation to the mainland, the whole production, manufacturing and assembly process for Tibetan alpine snow original products <br> The fresh air from Himalaya can still be touched for the first time after opening the product gently.'
            },
            {
              type: 'chinese',
              content: '生产及销售，建设了世界上海拔最高的牦牛乳冰淇淋工厂 。 打造了全球首款，西藏原产的牦牛乳冰淇淋系列产品，六种经典口味，皆是用心之作，牦牛乳原味冰淇淋、牦牛喜马拉雅粉盐味冰淇淋、牦牛乳藏式糌粑味冰淇淋、牦牛乳藏式酸奶味冰淇淋、牦牛乳藏式奶茶味冰淇淋、牦牛乳青稞酒味冰淇淋……喜悦不仅纯粹！还充满期许！我们在世界屋脊之上，在3623米的地方，历经艰辛，建造诺布庄园——世界上海拔最高的冰淇淋工厂'
            },
            {
              type: 'chinese',
              content: '倾尽一切，精心打造诺布庄园品牌。联手最强大的物流，我们建立了多个冷库，不论你在何方，只要你轻轻点击下单，诺布庄园牦牛乳冰淇淋就会越过山、越过海，为你带来喜悦的味道。牦牛始终保持源种动物的天性，它不能被圈养，也难以规模化饲养 ，牦牛就这样自然地生长，奉献着珍贵的乳汁。'
            },
            {
              type: 'chinese',
              content: '每一杯诺布庄园牦牛乳冰淇淋，都将为藏区牧民更好的生活带来希望！你的选择，是喜悦和爱的传递。西藏原产，虔心奉献。诺布庄园牦牛乳冰淇淋，西藏的味道，喜悦的味道，诺布庄园，越来越爱........'
            }
          ]
        }
      }
    },
    mounted() {
      setTimeout(()=> {
        this.viewCFlag = true;
      })
    },
    created() {
      // console.log('create introPage');
    },
    methods: {},
    props: ['introPageData'],
    watch: {
      introPageData: {
        handler(newData,oldData) {
          // console.log('new',newData);
          // console.log('old',oldData);
          this.introPageData = newData;
          if(this.introPageData) {
            // this.firstPart = {...this.introPageData.firstPart};
            // this.secondPart = {...this.introPageData.secondPart};
            [this.firstPart,this.secondPart] = [{...this.introPageData.firstPart},{...this.introPageData.secondPart}];
          }
        },
        deep: true,
        immediate:true
      }
    }
  };
</script>

<style lang="less" scoped>
    .introPage {
        position: absolute;
        width: 100%;
        height: 100%;
        /*@font-face {*/
            /*font-family: 'DIN-Regular';*/
            /*src: url(../assets/font/DIN-Regular.ttf);*/
        /*};*/
        .viewC {
            position: relative;
            margin: 0 auto;
            /*width: 7.5rem;*/
            width: 100%;
            height: 3.91rem;
            overflow: hidden;
            /*background: url("../assets/img/introMainView.png") no-repeat center center;*/
            background-repeat: no-repeat;
            background-position: center center;
            background-size: cover;
            opacity: 0;
            transition: all 2s;
            .introLogo {
                margin: 0.9rem 0 0 1.66rem;
                width: 0.89rem;
                height: 0.34rem;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;
            }
            .introContent {
                margin: 0.11rem 0 0 1.63rem;
                /*width: 3.62rem;*/
                width: 4.5rem;
                font-size: 0.08rem;
                font-family: DIN-Regular;
                font-weight: 400;
                color: rgba(0,0,0,1);
                line-height: 1.4;
                & > p {
                    margin-bottom: 0.2rem;
                }
            }
        }
        .viewC.act {
            opacity: 1;
        }
        .introMain {
            margin: 0.38rem auto 0.58rem;
            width: 4.5rem;
            .introLogo {
                width: 0.89rem;
                height: 0.34rem;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;
            }
            .introContent {
                margin: 0.11rem 0 0 0;
                width: 3.62rem;
                font-size: 0.08rem;
                font-family: DIN-Regular;
                font-weight: 400;
                color: rgba(0,0,0,1);
                line-height: 1.4;
                & > p {
                    margin-bottom: 0.2rem;
                }
                & > p.cn {
                    width: 4.5rem;
                    font-family: SourceHanSansSC-Normal;
                    font-weight: 400;
                    color: rgba(89,87,87,1);
                }
            }
        }
    }
</style>


