<template>
    <div class="homePage">
        <div class="viewC">
            <transition-group name="flip-list" tag="ul" class="loopImgList">
                <li v-for="(item,index) in loopImgList" :key="item.imgLink" class="loopImgItem" v-show="currentIndex === index">
                    <img :src="item.imgLink" alt="">
                </li>
            </transition-group>
            <ul class="loopPointList">
                <li v-for="(item,_index) in loopImgList" :key="_index" class="loopImgItem"
                    :class="{'act':currentIndex === _index}" @click="selectLoopImg(_index)">
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'homePage',
    data() {
      return {
        loopImgList: [
          {
            imgLink: require('../assets/img/homeLoop_1.png'),
          },
          // {
          //   imgLink: require('../assets/img/homeLoop_2.png'),
          // },
          // {
          //   imgLink: require('../assets/img/homeLoop_3.png'),
          // },
          // {
          //   imgLink: require('../assets/img/homeLoop_4.png'),
          // }
        ],
        currentIndex: -1,
        loopTimer: null
      };
    },
    methods: {
      loopImg() {
        if (this.loopTimer) {
          clearInterval(this.loopTimer);
          this.loopTimer = null;
        }
        this.loopTimer = setInterval(() => {
          this.currentIndex++;
          if (this.currentIndex >= this.loopImgList.length) {
            this.currentIndex = 0;
          }
        }, 5500);
      },
      selectLoopImg(index) {
        console.log('index', index);
        this.currentIndex = index;
        this.loopImg();
      }
    },
    mounted() {
      this.currentIndex = 0;
      this.loopImg();
    },
    created() {
      // console.log('create HomePage');
    },
    destroyed() {
      if (this.loopTimer) {
        clearInterval(this.loopTimer);
        this.loopTimer = null;
      }
    },
    props: ['homePageBannerList'],
    watch: {
      homePageBannerList: {
        handler(newArr,oldArr) {
          // console.log('newArr',newArr);
          // console.log('oldArr',oldArr);
          this.homePageBannerList = newArr;
          if (this.homePageBannerList.length) {
            this.loopImgList = [...this.homePageBannerList];
          }
        },
        deep: true,
        immediate:true
      }
    }
  };
</script>

<style lang="less" scoped>
    .homePage {
        position: absolute;
        width: 100%;
        height: 100%;
        /*background: url("../assets/img/homeLoop_1.png") no-repeat center center;*/
        /*background-size: cover;*/
        .viewC {
            position: relative;
            margin: 0 auto;
            width: 100%;
            height: 3.91rem;
            overflow: hidden;
            & > .loopImgList {
                position: relative;
                & > li {
                    width: 7.5rem;
                    height: 3.91rem;
                    position: absolute;
                    top: 0;
                    left: 0;
                    text-align: center;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
            & > .loopPointList {
                position: absolute;
                left: 50%;
                bottom: 0.1rem;
                margin-left: -0.5rem;
                width: 1rem;
                height: 0.5rem;
                display: flex;
                justify-content: space-around;
                align-items: center;
                /*background-color: cyan;*/
                & > li {
                    width: 10px;
                    height: 10px;
                    border-radius: 10px;
                    background: rgba(131, 131, 131, 1);
                    transition: all 2s;
                    cursor: pointer;
                }
                & > li.act {
                    background: rgba(247, 248, 248, 1);
                }
                @media screen and (max-width: 400px) {
                    & > li {
                        width: 6px;
                        height: 6px;
                        border-radius: 6px;
                    }
                }
            }
            /*
                v-enter: 定义进入过渡的开始状态。在元素被插入之前生效，在元素被插入之后的下一帧移除。
                v-enter-active: 定义进入过渡生效时的状态。在整个进入过渡的阶段中应用，在元素被插入之前生效，在过渡/动画完成之后移除。这个类可以被用来定义进入过渡的过程时间，延迟和曲线函数。
                v-enter-to: 2.1.8版及以上 定义进入过渡的结束状态。在元素被插入之后下一帧生效 (与此同时 v-enter 被移除)，在过渡/动画完成之后移除。
                v-leave: 定义离开过渡的开始状态。在离开过渡被触发时立刻生效，下一帧被移除。
                v-leave-active：定义离开过渡生效时的状态。在整个离开过渡的阶段中应用，在离开过渡被触发时立刻生效，在过渡/动画完成之后移除。这个类可以被用来定义离开过渡的过程时间，延迟和曲线函数。
                v-leave-to: 2.1.8版及以上 定义离开过渡的结束状态。在离开过渡被触发之后下一帧生效 (与此同时 v-leave 被删除)，在过渡/动画完成之后移除。

            */
            .flip-list-enter-active, .flip-list-leave-active {
                transition: opacity 5s ease;
            }
            .flip-list-enter, .flip-list-leave-active {
                opacity: 0;
            }
        }
        .LoopC {
            width: 100%;
            height: 100%;
            overflow: hidden;
            img {
                width: 100%;
            }
        }
    }
</style>


