<template>
    <div class="indexPage" ref="indexPage">
        <div class="navContainer" v-show="currentNavType === navTypeSet.home">
            <div class="logoC" @click="selectToPage"></div>
            <div class="homeTitle" @click="selectToPage(pageTypeSet.homePage)" :class="{'act':currentComponentType === pageTypeSet.homePage}">首页</div>
            <div class="cutLine"></div>
            <div class="productTitle" @click="selectToPage(pageTypeSet.productPage)" :class="{'act':currentComponentType === pageTypeSet.productPage}">产品</div>
            <div class="cutLine"></div>
            <div class="introTitle" @click="selectToPage(pageTypeSet.introPage)" :class="{'act':currentComponentType === pageTypeSet.introPage}">诺布</div>
        </div>
        <div class="navContainer_w" v-show="currentNavType === navTypeSet.other">
            <div class="logoC" @click="selectToPage"></div>
            <div class="homeTitle" @click="selectToPage(pageTypeSet.homePage)" :class="{'act':currentComponentType === pageTypeSet.homePage}">首页</div>
            <div class="cutLine"></div>
            <div class="productTitle" @click="selectToPage(pageTypeSet.productPage)" :class="{'act':currentComponentType === pageTypeSet.productPage}">产品</div>
            <div class="cutLine"></div>
            <div class="introTitle" @click="selectToPage(pageTypeSet.introPage)" :class="{'act':currentComponentType === pageTypeSet.introPage}">诺布</div>
        </div>
        <component :is="currentComponentType" :homePageBannerList="homePageBannerList" :productPageData="productPageData" :introPageData="introPageData"></component>
        <!--<keep-alive></keep-alive>-->

    </div>
</template>

<script>
  import HomePage from './homePage';
  import IntroPage from './introPage';
  import ProductPage from './productPage';
  export default {
    name: 'index',
    data() {
      return {
        currentComponentType: 'HomePage',
        pageTypeSet: {
          homePage: 'HomePage',
          productPage: 'ProductPage',
          introPage: 'IntroPage',
        },
        lastSelectedPage: '',
        currentNavType: 0, // 0 首页  1 其他
        navTypeSet: {
          home: 0,
          other: 1
        },
        homePageBannerList: [],
        productPageData: null,
        introPageData: null
      }
    },
    methods: {
      selectToPage(type) {
            switch (type) {
              case this.pageTypeSet.homePage:
                this.currentComponentType = type;
                this.currentNavType = this.navTypeSet.home;
                break;
              case this.pageTypeSet.productPage:
                this.currentComponentType = type;
                this.currentNavType = this.navTypeSet.other;
                break;
              case this.pageTypeSet.introPage:
                this.currentComponentType = type;
                this.currentNavType = this.navTypeSet.home;
                break;
              default:
                // window.location.reload();
                this.currentComponentType = this.pageTypeSet.homePage;
                this.currentNavType = this.navTypeSet.home;
                break;
            }
            // TODO: 页面回到初始状态
            [window.pageYOffset,document.documentElement.scrollTop,document.body.scrollTop] = [0,0,0];
        },
      getHomePageBannerList() {
        let url = '/api.php/api/Api/getHomePageBannerList';
        this.$ajax(url, {}, res => {
          if (res.data.code == 200) {
            this.homePageBannerList = [...res.data.data];
          }
        });
      },
      getProductPageData() {
        let url = '/api.php/api/Api/getProductDataArray';
        this.$ajax(url, {}, res => {
          if (res.data.code == 200) {
            this.productPageData = {...res.data.data};
            // console.log('this.productPageData',this.productPageData);
          }
        });
      },
      getIntroPageData() {
        let url = '/api.php/api/Api/getNuoBoolArray';
        this.$ajax(url, {}, res => {
            this.introPageData = {...res.data.data};
            // console.log('this.introPageData',this.introPageData);
        });
      },
      handleScroll () {
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        console.log('window.pageYOffset',window.pageYOffset);
        console.log('document.documentElement.scrollTop',document.documentElement.scrollTop);
        console.log('document.body.scrollTop',document.body.scrollTop)
      }
      // preLoadImg(obj,index) {
      //   let img = new Image();
      //   img.onload = ()=> {};
      //   img.src = obj.imgLink
      // },
    },
    mounted() {
      this.getHomePageBannerList();
      this.getProductPageData();
      this.getIntroPageData();
      // window.addEventListener('scroll', this.handleScroll);
    },
    components: {
      HomePage,
      IntroPage,
      ProductPage
    }
  };
</script>
<style lang="less" scoped>
    .indexPage {
        .navContainer {
            position: fixed;
            left: 50%;
            margin-left: -2.34rem;
            width: 4.68rem;
            height: 0.30rem;
            background-color: rgba(0,1,17,1);
            z-index: 99;
            display: flex;
            align-items: center;
            font-size: 0.08rem;
            font-weight: 400;
            color: rgba(255,255,255,1);
            text-align: center;
            .logoC {
                margin:0 0.4rem 0 0.23rem;
                width: 1.15rem;
                height: 0.19rem;
                background: url("../assets/img/logo_home.png") no-repeat center center;
                background-size: cover;
                cursor: pointer;
            }
            .homeTitle, .introTitle {
                width: 0.5rem;
                cursor: pointer;
            }
            .cutLine {
                width: 1px;
                height: 0.06rem;
                background-color: #ffffff;
            }
            .productTitle {
                width: 0.5rem;
                cursor: pointer;
            }
            .act {
                color: #00a4ff;
            }

        }
        .navContainer_w {
            position: fixed;
            left: 50%;
            margin-left: -2.34rem;
            width: 4.68rem;
            height: 0.30rem;
            background:rgba(255,255,255,1);
            z-index: 99;
            display: flex;
            align-items: center;
            font-size: 0.08rem;
            font-weight: 400;
            color: #000000;
            text-align: center;
            border-bottom: 1px solid #F0F0F0;
            .logoC {
                margin:0 0.4rem 0 0.23rem;
                width: 1.15rem;
                height: 0.19rem;
                background: url("../assets/img/logo.png") no-repeat center center;
                background-size: cover;
                cursor: pointer;
            }
            .homeTitle, .introTitle {
                width: 0.5rem;
                cursor: pointer;
            }
            .cutLine {
                width: 1px;
                height: 0.06rem;
                background-color: #000000;
            }
            .productTitle {
                width: 0.5rem;
                cursor: pointer;
            }
            .act {
                color: #00a4ff;
            }
        }
    }
</style>


