import Vue from 'vue';
import axios from 'axios';
import qs from 'qs';

// TODO: 生成环境写的是完整路径，能访问。

const AJAXURL = process.env.NODE_ENV === 'development' ? '/api' : process.env.API_ROOT;

//k扩展的请求的方法
Vue.prototype.$ajax = function(url, data, callback, type = 'post') {
    let self = this;
    let options = {};
    if (type === 'get') {
        data = JSON.stringify(data) === '{}' ? '' : '?' + qs.stringify(data);
        options = {
            method: type,
            url: AJAXURL + url + data,
        };
    } else if(type === 'post') {
        options = {
            method: type,
            url: AJAXURL + url,
            data: data,
        };
    }

    axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
    axios(options).then(function(response) {
        if(callback) callback(response);
        // console.log('res',response);
    }).catch(function(err) {
        if(callback) callback(null,err);
        // console.log('error',err);
    });
}



