<template>
    <div class="productPage" ref="productPage">
        <div class="viewC">
            <transition-group name="flip-list" tag="ul" class="loopImgList">
                <li v-for="(item,index) in loopImgList" :key="item.imgLink" class="loopImgItem" v-show="currentIndex === index">
                    <img :src="item.imgLink" alt="">
                </li>
            </transition-group>
            <ul class="loopPointList">
                <li v-for="(item,_index) in loopImgList" :key="_index" class="loopImgItem" :class="{'act':currentIndex === _index}" @click="selectLoopImg(_index)"></li>
            </ul>
        </div>
        <div class="recommendC">
            <div class="titleC">
                <hr>
                <div class="titleText">{{titleText}}</div>
                <hr>
            </div>
            <ul class="iceCreamList_A">
                <li class="item_A" v-for="(item,index) in iceCreamAList" :key="index" :style="{backgroundImage:'url('+item.backgroundImg+')'}">
                    <div class="item_content_right" v-if="item.direction === 'right'">
                        <div class="item_content_title">
                            <div>{{item.chineseTitle}}</div>
                            <ul>
                                <li v-for="titleText in item.englishTitle" :key="titleText">{{titleText.toUpperCase()}}</li>
                            </ul>
                        </div>
                        <div class="item_content_tip">{{item.tipName}}</div>
                        <div class="item_content_ice">
                            <div class="ice_price">￥<span>{{item.productPrice.split('.')[0]}}</span>.{{item.productPrice.split('.')[1]}}</div>
                            <div class="ice_des">{{item.productTib}}</div>
                        </div>
                    </div>
                    <div class="item_content_left" v-if="item.direction === 'left'">
                        <div class="item_content_ice">
                            <div class="ice_price">￥<span>{{item.productPrice.split('.')[0]}}</span>.{{item.productPrice.split('.')[1]}}</div>
                            <div class="ice_des">{{item.productTib}}</div>
                        </div>
                        <div class="item_content_tip">{{item.tipName}}</div>
                        <div class="item_content_title">
                            <div>{{item.chineseTitle}}</div>
                            <ul>
                                <li v-for="titleText in item.englishTitle" :key="titleText">{{titleText.toUpperCase()}}</li>
                            </ul>
                        </div>
                    </div>
                    <div class="item_iconC item_iconC_right" v-if="item.direction === 'right'" :style="{backgroundImage:'url('+item.productImg+')'}"></div>
                    <div class="item_iconC item_iconC_left" v-if="item.direction === 'left'" :style="{backgroundImage:'url('+item.productImg+')'}"></div>
                </li>
            </ul>
            <ul class="iceCreamList_B">
                <li class="item_B" v-for="(item,index) in iceCreamBList" :key="index">
                    <div class="item_iconC" :style="{backgroundImage:'url('+item.productImg+')'}"></div>
                    <div class="item_title_cn">{{item.chineseTitle}}</div>
                    <ul class="item_title_en">
                        <li v-for="titleText in item.englishTitle" :key="titleText">{{titleText.toUpperCase()}}</li>
                    </ul>
                </li>
            </ul>
        </div>
        <div class="footerC">
            <ul class="footerTextC">
                <li class="footerTextItem" v-for="itemText in footerList" :key="itemText">{{itemText}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'productPage',
    data() {
      return {
        loopImgList: [
          {
            imgLink: require('../assets/img/productLoop_1.png'),
          },
          {
            imgLink: require('../assets/img/productLoop_2.png'),
          },
          // {
          //   imgLink: require('../assets/img/productLoop_3.png'),
          // },
          // {
          //   imgLink: require('../assets/img/productLoop_4.png'),
          // }
        ],
        currentIndex: -1,
        loopTimer: null,
        titleText: '店铺推荐',
        iceCreamAList: [
          {
            direction: 'right',
            backgroundImg: require('../assets/img/ice-cream-A-01.png'),
            productImg: require('../assets/img/icon-A-01.png'),
            tipName: '专业冷链物流, 极速配送',
            chineseTitle: '耗牛乳原味冰激凌',
            englishTitle: [
              'nuobooland',
              'yak milk ice cream'
            ],
            productPrice: '35.00',
            productTib: '新品上市'
          },
          {
            direction: 'left',
            backgroundImg: require('../assets/img/ice-cream-A-02.png'),
            productImg: require('../assets/img/icon-A-02.png'),
            tipName: '专业冷链物流, 极速配送',
            chineseTitle: '耗牛乳原味冰激凌',
            englishTitle: [
              'nuobooland',
              'yak milk ice cream'
            ],
            productPrice: '35.00',
            productTib: '新品上市'
          }
        ],
        iceCreamBList: [
          {
            productImg: require('../assets/img/icon-B-01.png'),
            chineseTitle: '耗牛乳原味冰激凌',
            englishTitle: [
              'nuobooland',
              'yak milk ice cream'
            ],
          },
          {
            productImg: require('../assets/img/icon-B-02.png'),
            chineseTitle: '耗牛乳原味冰激凌',
            englishTitle: [
              'nuobooland',
              'yak milk ice cream'
            ],
          }
        ],
        footerList: [
          '2019 All rights reseved .desin.By Nuobooland',
          '京IPC备124566543号-2-营业执照'
        ]
      }
    },
    methods: {
      loopImg() {
        if (this.loopTimer) {
          clearInterval(this.loopTimer);
          this.loopTimer = null;
        }
        this.loopTimer = setInterval(()=>{
          this.currentIndex++;
          if (this.currentIndex >= this.loopImgList.length) {
            this.currentIndex = 0;
          }
        },5500);
      },
      selectLoopImg(index) {
        this.currentIndex = index;
        this.loopImg();
      }

    },
    mounted() {
      this.currentIndex = 0;
      this.loopImg();
      // console.log('this.$refs.productPage.scrollTop',this.$refs.productPage.scrollTop);
      // console.log('document.body.scrollTop',document.body.scrollTop);

    },
    created() {
      // console.log('create productPage');
    },
    destroyed() {
      if (this.loopTimer) {
        clearInterval(this.loopTimer);
        this.loopTimer = null;
      }
    },
    props: ['productPageData'],
    watch: {
      productPageData: {
        handler(newData,oldData) {
          // console.log('new',newData);
          // console.log('old',oldData);
          this.productPageData = newData;
          if (this.productPageData) {
            [this.loopImgList,this.iceCreamAList,this.iceCreamBList,this.footerList] = [[...this.productPageData.banner],[...this.productPageData.storeProduct],[...this.productPageData.productList],[...this.productPageData.footer.copy]];
            // this.loopImgList = [...this.productPageData.banner];
            // this.iceCreamAList = [...this.productPageData.storeProduct];
            // this.iceCreamBList = [...this.productPageData.productList];
            // this.footerList = [...this.productPageData.footer.copy];
          }
        },
        deep: true,
        immediate:true
      }
    }
  };
</script>

