                    background:rgba(247,248,248,1);
                }
                @media screen and (max-width: 400px) {
                    & > li {
                        width: 6px;
                        height: 6px;
                        border-radius: 6px;
                    }
                }
            }

            .flip-list-enter-active, .flip-list-leave-active{
                transition: opacity 5s ease;
            }
            .flip-list-enter, .flip-list-leave-active {
                opacity: 0;
            }

        }
        .recommendC {
            width: 7.5rem;
            margin-top: 0.28rem;
