                    margin: 0 0.22rem;
                }
            }
            .iceCreamList_A {
                margin: 0 auto;
                /*width: 6.88rem;*/
                width: 4.47rem;
                & > li {
                    position: relative;
                    width: 100%;
                    /*height: 1.8rem;*/
